.masonry-grid:after {
  content: '';
  display: block;
  clear: both;
}

/* ---- .grid-item ---- */ 

.masonry-grid-item {
  float: left;
  padding: 0;
  cursor: -webkit-zoom-in;
  cursor: zoom-in;
  /* transition: opacity .5s ease; */
  background-size: 100%;
  background-repeat: no-repeat;

}

.masonry-grid-item img {
  display: block;
  max-width: 100%;
}

.masonry-grid-item:hover {
  opacity: 0.5;
}
