  -webkit-linear-gradient(335deg, #1E1E24 100%, rgba(0, 0, 0, 0) 0%);
  background: linear-gradient(115deg, #26262d 100px, rgba(0, 0, 0, 0) 0%),
  linear-gradient(115deg, #1E1E24 300px, rgba(0, 0, 0, 0) 0%),
  linear-gradient(115deg, #26262d 500px, rgba(0, 0, 0, 0) 0%),
  linear-gradient(115deg, #1E1E24 100%, rgba(0, 0, 0, 0) 0%);
  font-size: 1em;
  color: #fff;
}

#parallax-content-two {
  border-top: solid 2px #EEB902;
  background-color: #1E1E24;
}

.about-row {
  padding: 25px;
}

#parallax-content-two div {
  color: #CDD3CE;
}

#parallax-content-two h1 {
  position: absolute;
  top: 50px;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  color: #fff;
}

#parallax-content-two h5 {
  position: absolute;
  top: 120px;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  margin-bottom: 30px
}

#parallax-content-two h3 {
  font-size: 1.3em;
  color: #fff;
}

#parallax-content-two h6 {
  font-size: 0.9em;
  line-height: 30px;
}

#parallax-content-two .about-img {
  width: 100%;
  object-fit: cover;
  max-height: 300px;
}

#parallax-content-two .about-img-content {
  object-fit: cover;
  width: 100%;
  max-height: 150px;
  max-width: 150px;
  border-radius: 50%;
}

#parallax-content-two p {
  width: 100%;
  text-align: center;
}

#parallax-content-two a {
  text-align: right;
  color: #CDD3CE;
}

#parallax-content-two a:hover {
  color: #EEB902;
}

.parallax .caption {
  color: #CDD3CE;
  text-align: center;
}

.parallax > .caption > .caption-content {
  padding: 20px;
  opacity: 0.85;
  border-radius: 5px;
}

.caption-content img {
  object-fit: scale-down;
  width: 100%;
  height: 100px;
}

.caption-content h1 {
  font-weight: bold;
  padding: 10px 0;
  letter-spacing: 15px;
  color: #fff;
}

.arrow {
  text-align: center;
  margin-top: 40px;
}

.bounce {
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}

.arrow a {
  color: #CDD3CE;
  text-decoration: none;
}

.arrow a:hover {
  color: #EEB902;
}

@-webkit-keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
  40% {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
  }
  60% {
      -webkit-transform: translateY(-15px);
              transform: translateY(-15px);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
  40% {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
  }
  60% {
      -webkit-transform: translateY(-15px);
              transform: translateY(-15px);
  }
}

#parallax-content-one #goto-content-one {
  margin-top: -60px; /* Size of fixed header */
  padding-bottom: 60px;
  display: block;
}

@media screen and (max-width: 767px) {
  #parallax-content-one h4 {
      font-size: 1em;
  }

  #parallax-content-one h5 {
      font-size: 0.85em;
  }

  #parallax-content-one h6 {
      font-size: 0.8em;
  }

  #parallax-content-one .parallax-content-btngroup {
      padding: 10px;
  }

  #parallax-content-one .parallax-content-btngroup a {
      margin: 5px 0;
  }

  #parallax-content-two h1 {
      font-size: 1.8em;
      top: 25px;
  }

  #parallax-content-two h5 {
      font-size: 0.9em;
      top: 80px;
  }

  #parallax-content-two h6 {
      font-size: 0.8em;
  }
}

